import React, { useEffect, useState, useRef } from "react";
import queryString from "query-string";
import io from "socket.io-client";
import * as conversation from "../constants/conversation";
import { Col, Row, Container, Form } from "react-bootstrap";
import InfoBar from "./InfoBar";
import Messages from "./Messages";
import Input from "./Input";
import throttle from "lodash/throttle";

let socket;

const Chat = ({ location }) => {
  const [name, setName] = useState("");
  const [room, setRoom] = useState("");
  const [message, setMessage] = useState("");
  const [messages, setMessages] = useState([]);
  const useDebouncedEffect = (effect, deps, delay) => {
    useEffect(() => {
      const handler = setTimeout(() => effect(), delay);

      return () => clearTimeout(handler);
    }, [...(deps || []), delay]);
  };

  useEffect(() => {
    let { name, room } = queryString.parse(location.search);
    socket = io(conversation.host);
    setRoom(room);
    setName(name);
    //login chat [dismount handle]
    socket.emit("join", { name, room }, () => {});
    //log out chat [unmount handle]
    return () => {
      socket.emit("disconnect");
      socket.off();
    };
  }, [conversation.host, location.search]);

  const sendMessage = (event) => {
    event.preventDefault();
    if (message) {
      socket.emit("sendMessage", message, () => {
        setMessage("");
      });
    }
  };

  useDebouncedEffect(
    () => {
      socket.on("message", (message) => {
        setMessages([...messages, message]);
      });
    },
    [messages],
    300
  );

  return (
    <Container className="outerContainer ">
      <Row>
        <InfoBar room={room} />
        <Messages messages={messages} name={name} />
        <Input
          message={message}
          setMessage={setMessage}
          sendMessage={sendMessage}
        />
      </Row>
    </Container>
  );
};

export default Chat;
